.fullDialogs {
    height: 100%;
    min-height: 100%;
}

.dialogs {
    display: grid;
    grid-template-columns: 3fr 10fr 3fr;
    grid-gap: 2em;
    margin: 10px;
}

.dialogsItem {
    display: grid;
    grid-template-rows: 50px;
    margin-left: 20px;
    border-style: groove;
    border-radius: 20px;
}

.dialogsItem img {
    width: 50px;
    height: 50px;
    border-radius: 30px;
}

.active {
    color: gold;
}

.buttonSend {
    position: absolute;
    padding: 5px;
    width: 70px;
    height: 35px;
}